import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { ComprasService } from 'src/app/services/compras.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { Utilidades } from 'src/app/services/utilidades';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { CatalogoComponent } from 'src/app/views/pages/catalogo/catalogo.component';
import swal from 'sweetalert2';
import { UnpaidOrderComponent } from '../../base/unpaid-order/unpaid-order.component';

@Component({
  selector: 'app-car-shop',
  templateUrl: './car-shop.component.html',
  styleUrls: ['./car-shop.component.scss']
})
export class CarShopComponent implements OnInit {

  private utilidades: Utilidades = new Utilidades();
  public static carrito: any[] = [];
  public totalPagar: number;
  private modalCarrito: NgbModalRef;
  public pedido: {
    productos: any,
    tipoPago: number,
    direccion: string,
    miDireccion: boolean,
    sinFactura: boolean,
    archivo: number
  };
  public miDireccion: boolean;
  private modalPagos: NgbModalRef;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public pagoTipos: any[];
  public cantidadProducto: number;
  public producto: any;
  private cantidadModal: NgbModalRef;
  public formEvidenciaPago: FormGroup;

  constructor(private ngbModal: NgbModal,
    private pedidoService: PedidoService,
    private router: Router,
    private catalogoService: CatalogoService,
    private comprasService: ComprasService) { }

  ngOnInit(): void {
    this.pagoTipos = [];
    this.modalPagos = null;
    this.pedido = {
      productos: null,
      tipoPago: null,
      direccion: null,
      miDireccion: null,
      sinFactura: null,
      archivo: null
    };
    this.totalPagar = 0;
    if (this.utilidades.getUsuarioLogeado() && this.utilidades.getUsuarioLogeado().roles[0].authority == "ROLE_CLIENTE") {
      if (this.getCarritoSave()) {
        if (this.getCarritoSave().length > 0) {
          CarShopComponent.carrito = this.getCarritoSave();
        }
      }
    }
  }

  public getComprasInformation(): void {
    LoaderComponent.status = true;
    this.comprasService.getInformationCompras().subscribe(
      response => {
        UnpaidOrderComponent.comprasInformation = response;
        UnpaidOrderComponent.comprasInformation.totalGastos = UnpaidOrderComponent.comprasInformation.totalGastos == null ? 0 : UnpaidOrderComponent.comprasInformation.totalGastos;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public changeTipo(event: any): any {
    if (event != undefined) {
      if (event.id) {
        this.pedido.tipoPago = event.id;
      }
    }
  }

  public cleanTipo(): void {
    this.pedido.tipoPago = null;
  }

  public cambiarEstadoDireccion(estado: boolean): void {
    if(!estado) {
      this.formEvidenciaPago.addControl("direccion", new FormControl("", [Validators.required, Validators.minLength(5), Validators.maxLength(100)]));
    } else {
      this.formEvidenciaPago.removeControl("direccion");
    }
    this.miDireccion = estado;
  }

  public viewPedidos(): void {
    this.modalCarrito.close();
    this.router.navigate(["/cliente/compras"]);
  }

  public closeCantidad(): void {
    this.cantidadModal.close();
  }

  public saveCarrito(): void {
    if (sessionStorage.getItem("carrito")) {
      sessionStorage.removeItem("carrito");
    }
    sessionStorage.setItem("carrito", JSON.stringify(CarShopComponent.carrito));
  }

  public getCarritoSave(): any {
    return JSON.parse(sessionStorage.getItem("carrito"));
  }

  public openComprar(content: any): void {
    if (CarShopComponent.carrito.length > 0) {
      LoaderComponent.status = true;
      this.miDireccion = true;
      this.formEvidenciaPago = new FormGroup({
        tipoPago: new FormControl("", [Validators.required])
      });
      this.pedidoService.consultarTipoPagos().subscribe(
        response => {
          this.pagoTipos = response;
          LoaderComponent.status = false;
          this.modalPagos = this.ngbModal.open(content, { size: 'xl' });
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      swal.fire("¡Selecciona un producto!", "", "warning");
    }
  }

  public closeComprar(): any {
    this.modalPagos.close();
  }

  public getRolUser(): string {
    return this.utilidades.getUsuarioLogeado().roles[0].authority;
  }

  public getCarrito(): any[] {
    return CarShopComponent.carrito;
  }

  public openCarrito(content: any): void {
    this.totalPagar = 0;
    CarShopComponent.carrito.forEach(producto => {
      this.totalPagar += producto.producto.precio * producto.cantidad;
    });
    this.modalCarrito = this.ngbModal.open(content, { size: "xl" });
  }

  public eliminarProducto(producto: any): void {
    swal.fire({
      title: '¿Estas seguro de eliminar el producto?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        for (let index = 0; index < CarShopComponent.carrito.length; index++) {
          let productoObj: any = CarShopComponent.carrito[index];
          if (productoObj.productoSelect.id == producto.id) {
            this.totalPagar = this.totalPagar - (productoObj.cantidad * productoObj.producto.precio);
            CarShopComponent.carrito.splice(index, 1);
            sessionStorage.removeItem("carrito");
            if (CarShopComponent.carrito.length > 0) {
              sessionStorage.setItem("carrito", JSON.stringify(CarShopComponent.carrito));
            }
            swal.fire("¡Eliminado!", "¡Producto: " + productoObj.producto.nombre + " eliminado!", "success");
            LoaderComponent.status = false;
            return;
          }
        }
      }
    });
  }

  public getDatosUsuario(): any {
    this.utilidades = new Utilidades();
    return this.utilidades.getUsuarioLogeado();
  }

  public consultarProductos(pagina: number, totalRegistros: number, nombre: string): any {
    LoaderComponent.status = true;
    this.catalogoService.findProductos(pagina, totalRegistros, nombre).subscribe(
      response => {
        CatalogoComponent.productos = response.content;
        this.paginadorInit(response);
        setTimeout(p => {
          LoaderComponent.status = false;
        }, 200);
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        setTimeout(p => {
          LoaderComponent.status = false;
        }, 200);
      }
    );
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    CatalogoComponent.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

  public crearPedido(): void {
    LoaderComponent.status = true;
    if (CarShopComponent.carrito.length > 0) {
      let productos: any[] = [];
      CarShopComponent.carrito.forEach(producto => {
        productos.push({
          id: producto.productoSelect.id,
          cantidad: producto.cantidad
        });
      });
      this.pedido.productos = productos;
      if(this.formEvidenciaPago.valid){
        if (this.miDireccion) {
          this.pedido.direccion = this.getDatosUsuario().usuario.direccion;
        } else {
          this.pedido.direccion = this.formEvidenciaPago.get("direccion").value;
        }
        this.pedido.sinFactura = false;
        this.pedidoService.crearPedido(this.pedido).subscribe(
          response => {
            swal.fire("¡Creado!", "Tu pedido ha sido creado, numero de pedido:" + response.id, "success");
            CarShopComponent.carrito = [];
            sessionStorage.removeItem("carrito");
            this.consultarProductos(1, 10, "all");
            this.modalPagos.close();
            this.formEvidenciaPago.reset();
            this.ngOnInit();
            this.getComprasInformation();
            LoaderComponent.status = false;
          },
          error => {
            this.modalCarrito.close();
            this.modalPagos.close();
            this.mensajesComponent.mensajesError(error);
            LoaderComponent.status = false;
          }
        );
      }
    } else {
      swal.fire("¡Selecciona un producto!", "", "warning");
      LoaderComponent.status = false;
    }
  }

}
