import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class NotificationsService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http:HttpClient) { }

  public deleteAllNotification(): Observable<any>{
    return this.http.delete(environment.backEndPoint + "/api/notificaciones/deleteAll", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public deleteNotificationByIdNotification(notificationId: number): Observable<any>{
    return this.http.delete(environment.backEndPoint + "/api/notificaciones/delete/" + notificationId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getNotifications(page: number, contentSize: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/notificaciones/get/" + page + "/" + contentSize, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }
}
