import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Utilidades } from '../services/utilidades';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PedidoService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public uploadReciboVenta(ventaId: number, archivo: FormData): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/venta/add/recibo/" + ventaId, archivo, {headers: this.utilidades.getHeadersArchivo()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public crearPedido(pedido: any): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/venta/crear", pedido, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public consultarTipoPagos(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/tipo/find/tipoPagos", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
