import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class QuejasService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public getRespuestasByQuejaIdAndUserId(page : number, size: number, quejaId: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/queja/get/respuestasByQuejaAndUser/" + page + "/" + size + "/" + quejaId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getRespuestasByQuejaId(page : number, size: number, quejaId: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/queja/get/respuestasByQueja/" + page + "/" + size + "/" + quejaId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createRespuesta(quejaId: number, respuesta: string): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/queja/create/respuesta/" + quejaId + "?respuesta=" + respuesta, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public quejasViews(quejasIds: number[]): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/queja/quejas/views", quejasIds, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getAllQuejasNotView(page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/queja/get/quejasNotView/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getQuejasByMensajeAndVentaId(ventaId: number, mensaje: string, page: number, totalElements: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/queja/get/byVentaId/mensaje/" + page + "/" + totalElements + "?ventaId=" + ventaId + "&mensaje=" + mensaje, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getQuejasByventaId(ventaId: number, page: number, totalElements: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/queja/get/byVentaId/" + page + "/" + totalElements + "?ventaId=" + ventaId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public respuestasViews(respuestasIds: number[]): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/queja/respuestas/views", respuestasIds, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createQueja(queja: string, pedidoId: number): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/queja/create?queja=" + queja + "&pedidoId=" + pedidoId, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getRespuestas(page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/queja/getRespuestas/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }
}
