import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ComprasService } from 'src/app/services/compras.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { Utilidades } from 'src/app/services/utilidades';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-unpaid-order',
  templateUrl: './unpaid-order.component.html',
  styleUrls: ['./unpaid-order.component.scss']
})
export class UnpaidOrderComponent implements OnInit {

  public static comprasInformation: any;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  private modalPedidoSinPagar: NgbModalRef;
  public reciboDePago: File;
  private utilidades: Utilidades = new Utilidades();

  constructor(private pedidoService: PedidoService, private comprasService: ComprasService, private router: Router, private modalService: NgbModal) { }

  ngOnInit(): void {
    this.reciboDePago = null;
    UnpaidOrderComponent.comprasInformation = null;
    if (this.utilidades.getUsuarioLogeado() && this.utilidades.getUsuarioLogeado().roles[0].authority == "ROLE_CLIENTE") {
      LoaderComponent.status = true;
      this.comprasService.getInformationCompras().subscribe(
        response => {
          UnpaidOrderComponent.comprasInformation = response;
          UnpaidOrderComponent.comprasInformation.totalGastos = UnpaidOrderComponent.comprasInformation.totalGastos == null ? 0 : UnpaidOrderComponent.comprasInformation.totalGastos;
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    }
  }

  public getComprasinformation(): any {
    return UnpaidOrderComponent.comprasInformation;
  }

  public openFileBrowser(event: any): void {
    event.preventDefault();
    let element: HTMLElement = document.querySelector("#fileUploadInputExample") as HTMLElement;
    element.click()
  }

  public openPedidoSinPagar(content: any): void {
    this.modalPedidoSinPagar = this.modalService.open(content, { size: "xl" });
  }

  public handleFileInput(event: any): void {
    if (event.target.files.length) {
      let element: HTMLElement = document.querySelector("#fileUploadInputExample + .input-group .file-upload-info") as HTMLElement;
      let fileName = event.target.files[0].name;
      this.reciboDePago = event.target.files[0];
      element.setAttribute('value', fileName)
    }
  }

  public uploadReciboPago(): void {
    if (this.reciboDePago != null) {
      swal.fire({
        title: '¿Estas seguro de guardar este recibo?',
        text: "",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Si!',
        cancelButtonText: 'No!'
      }).then((result) => {
        if (result.isConfirmed) {
          LoaderComponent.status = true;
          let archivo: FormData = new FormData();
          archivo.append("file", this.reciboDePago);
          this.pedidoService.uploadReciboVenta(UnpaidOrderComponent.comprasInformation.pedidosSinPago.id, archivo).subscribe(
            response => {
              this.modalPedidoSinPagar.close();
              swal.fire("¡Recibo guardado!", "Gracias ya recivimos tu evidencia de pago, vamos a validar que tu pago sea correcto, para iniciar el proceso de preparación.", "success");
              this.ngOnInit();
              LoaderComponent.status = false;
            },
            error => {
              this.mensajesComponent.mensajesError(error);
              LoaderComponent.status = false;
            }
          );
        }
      });
    } else {
      swal.fire("¡Sube un recibo de pago!", "", "warning");
    }
  }

}
