import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { CatalogoService } from '../../../services/catalogo.service';
import { LoaderComponent } from '../../loader/loader.component';
import { MensajesComponent } from '../../mensajes/mensajes.component';
import swal from 'sweetalert2';
import { Utilidades } from 'src/app/services/utilidades';
import { NavbarComponent } from '../../layout/navbar/navbar.component';
import { CotizacionService } from 'src/app/services/cotizacion.service';
import { CarShopComponent } from '../../layout/navbar/car-shop/car-shop.component';

@Component({
  selector: 'app-catalogo',
  templateUrl: './catalogo.component.html',
  styleUrls: ['./catalogo.component.scss']
})
export class CatalogoComponent implements OnInit {

  public static productos: any;
  public static paginador: any;
  public cantidad: any;
  public categorias: any;
  public cantidadProducto: number;
  public producto: any;
  private modal: NgbModalRef;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public isCollapsed: boolean;
  private productoSelect: any;
  private cantidadModal: NgbModalRef;
  private utildades: Utilidades = new Utilidades();

  constructor(private cotizacionService: CotizacionService, private catalogoService: CatalogoService, private modalService: NgbModal, private router: Router, private activeRouter: ActivatedRoute) {
    if (this.activeRouter.snapshot.queryParams.accion) {
      swal.fire("¡Sesión cerrada!", "Esperamos que vuelvas pronto.", "success");
    }
  }

  ngOnInit(): void {
    this.productoSelect = null;
    this.cantidadProducto = 1;
    this.cantidad = 10;
    this.consultarProductos(1, this.cantidad, "all");
    LoaderComponent.status = true;
    this.catalogoService.findCategorias().subscribe(
      response => {
        this.categorias = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public addProductoCotizacion(): void {
    if (this.utildades.getUsuarioLogeado()) {
      if (this.cantidadProducto > 0) {
        LoaderComponent.status = true;
        this.cotizacionService.getCotizacion().subscribe(
          response => {
            if (response) {
              this.createProductoCotizacion(response.id);
            } else {
              LoaderComponent.status = false;
              swal.fire({
                title: '¡No tienes una cotización creada!',
                text: "¿Quieres crear una cotización?",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Si!',
                cancelButtonText: 'No!'
              }).then((result) => {
                if (result.isConfirmed) {
                  LoaderComponent.status = true;
                  this.cotizacionService.createCotizacion().subscribe(
                    response => {
                      swal.fire("¡Cotización creada con el No:" + response.id + "!");
                      this.createProductoCotizacion(response.id);
                    },
                    error => {
                      this.mensajesComponent.mensajesError(error);
                      LoaderComponent.status = false;
                    }
                  );
                }
              });
            }
          },
          error => {
            this.mensajesComponent.mensajesError(error);
            LoaderComponent.status = false;
          }
        );
      } else {
        swal.fire("¡Ingresa una cantidad valida!", "", "info");
      }
    } else {
      swal.fire("¡Inicia sesión!", "¡Para comprar un producto, necesitas iniciar sesión en el sistema!", "warning");
    }
  }

  public createProductoCotizacion(cotizacionId: number): void {
    this.cotizacionService.addProducto(this.productoSelect.id, this.cantidadProducto).subscribe(
      response => {
        swal.fire("¡Producto agregado a tu cotización No:" + cotizacionId + "!", "", "success");
        this.modal.close();
        this.cantidadModal.close();
        this.router.navigate(["/cliente/cotizaciones"]);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public saveCarrito(): void {
    if (sessionStorage.getItem("carrito")) {
      sessionStorage.removeItem("carrito");
    }
    sessionStorage.setItem("carrito", JSON.stringify(CarShopComponent.carrito));
  }

  public closeCantidad(): void {
    this.cantidadModal.close();
  }

  public openCantidad(content: any, producto: any): void {
    if (producto.cantidad <= 0) {
      swal.fire("¡Agotado!", "", "info");
    } else {
      this.productoSelect = producto;
      this.cantidadModal = this.modalService.open(content, { size: "lg" });
    }
  }

  public openDetalles(content: any, producto: any): void {
    this.producto = producto;
    this.modal = this.modalService.open(content, { size: 'xl' });
  }

  public agregarProducto(): void {
    if (this.utildades.getUsuarioLogeado()) {
      if (this.productoSelect) {
        const resultado: any = CarShopComponent.carrito.find(producto => producto.productoSelect.id == this.productoSelect.id);
        let cantidadActual: number = this.cantidadProducto;
        if (resultado) {
          cantidadActual += resultado.cantidad;
        }
        if (cantidadActual <= this.productoSelect.cantidad && this.cantidadProducto > 0) {
          let productoJSON: any = {
            productoSelect: this.productoSelect,
            producto: this.producto,
            cantidad: this.cantidadProducto
          };
          if (resultado) {
            resultado.cantidad += this.cantidadProducto;
          } else {
            CarShopComponent.carrito.push(productoJSON);
            this.saveCarrito();
          }
          swal.fire("¡Agregado!", "¡Producto agregado al carrito!", "success");
          this.cantidadProducto = 1;
          this.modal.close();
          this.cantidadModal.close();
        } else {
          if (resultado) {
            let cantidadActual: number = this.productoSelect.cantidad - resultado.cantidad;
            swal.fire("¡No tenemos existencias para esta cantidad, cantidad existente:" + cantidadActual + "!", "Si deseas puedes crear una cotización y enviarla a nosotros, para separar o negociar tu pedido.", "error");
          } else {
            swal.fire("¡No tenemos existencias para esta cantidad!", "", "error");
          }
          this.cantidadProducto = 1;
        }
      } else {
        swal.fire("¡Selecciona un producto!", "", "error");
      }
    } else {
      swal.fire("¡Inicia sesión!", "¡Para comprar un producto, necesitas iniciar sesión en el sistema!", "warning");
    }
  }

  public getProductos(): any {
    return CatalogoComponent.productos;
  }

  public getPaginador(): any {
    return CatalogoComponent.paginador;
  }

  public tipoDelete(): void {
    this.consultarProductos(1, this.cantidad, "all");
  }

  public changeTipo(tipo: any): void {
    if (tipo.id) {
      LoaderComponent.status = true;
      this.catalogoService.findForTipo(1, this.cantidad, tipo.id).subscribe(
        response => {
          CatalogoComponent.productos = response.content;
          this.paginadorInit(response);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    }
  }

  public cantidadChange(): void {
    this.consultarProductos(1, this.cantidad, "all");
  }

  public consultarProductos(pagina: number, totalRegistros: number, nombre: string): any {
    LoaderComponent.status = true;
    this.catalogoService.findProductos(pagina, totalRegistros, nombre).subscribe(
      response => {
        CatalogoComponent.productos = response.content;
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    CatalogoComponent.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

}
