import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class ComprasService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public getComprasGroupByYear(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/groupByYear", {headers:this.utilidades.getHeaders()}).pipe(
      catchError(error => {
        return throwError(error);
      })
    );
  }

  public getComprasGroupByMonthAndYear(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/groupByMonthAndYear", {headers:this.utilidades.getHeaders()}).pipe(
      catchError(error => {
        return throwError(error);
      })
    );
  }

  public getInformationCompras(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/getInfoVentas", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findCompras(pagina: number, cantidad: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/findForUser/" + pagina + "/" + cantidad, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error =>{
          return throwError(error);
        })
    )
  }

  public findComprasByIdOrDate(page: number, size: number, idOrden: number, date: Date): Observable<any>{
    let url: string = environment.backEndPoint + "/api/venta/findByIdOrFecha/" + page + "/" + size + "?";
    if(idOrden == null){
      url = url + "fecha=" + date;
    } else if (date == null) {
      url = url + "id=" + idOrden;
    }
    return this.http.get(url, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }
  
}
