import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Utilidades } from './utilidades';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UsuarioService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public getAllUsersByRol(rol: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/usuario/getByRol/" + rol + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getAllUsersByState(state: boolean, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/usuario/getByState/" + state.toString() + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getTotalData(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/usuario/getTotalData", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public changeStateUser(userId: number, state: boolean): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/usuario/changeState/" + userId + "/" + state.toString(), { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updateUser(userData: any, rolId: number): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/usuario/update?rolId=" + rolId, userData, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createUser(userData: any, rolId: number): Observable<any> {
    return this.http.post(environment.backEndPoint + "/api/usuario/create?rolId=" + rolId, userData, { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getAllRoles(): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/usuario/getAllRoles", { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findUsersByNameOrAll(name: string, page: number, size: number): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/usuario/findByNameOrAll/" + name + "/" + page + "/" + size, { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updatePassword(newPassword: string, oldPassword: string): Observable<any> {
    return this.http.put(environment.backEndPoint + "/api/usuario/update/password/" + newPassword + "/" + oldPassword, null, { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public actualizarCliente(userData: any): Observable<any> {
    return this.http.put(environment.backEndPoint + "/api/usuario/actualizar/cliente", userData, { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        })
    );
  }

  public login(correo: string, password: string): Observable<any> {
    return this.http.post(environment.backEndPoint + "/api/login", { username: correo, password: password }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public crearCliente(clienteDto: any): Observable<any> {
    return this.http.post(environment.backEndPoint + "/api/usuario/registrar/cliente", clienteDto).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public activarCuenta(token: string): Observable<any> {
    return this.http.post(environment.backEndPoint + "/verificacion/codigo/" + token, {}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public validarToken(token: string): Observable<any> {
    return this.http.post(environment.backEndPoint + "/verificacion/token/" + token, {}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
