import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BaseComponent } from './views/layout/base/base.component';
import { AuthGuard } from './core/guard/auth.guard';
import { NosotrosComponent } from './views/pages/nosotros/nosotros.component';
import { ContactanosComponent } from './views/pages/contactanos/contactanos.component';
import { IsLoginGuard } from './core/guard/is-login.guard';


const routes: Routes = [
  {
    path: '',
    component: BaseComponent,
    children: [
      { path: '', redirectTo: '/catalogo', pathMatch: 'full'},
      {
        path: "", loadChildren: () => import("./views/pages/catalogo/catalogo.module").then(m => m.CatalogoModule), canActivate: [IsLoginGuard]
      },
      {
        path: "nosotros", component: NosotrosComponent, canActivate: [IsLoginGuard]
      },
      {
        path: "contactanos", component: ContactanosComponent, canActivate: [IsLoginGuard]
      },
      {
        path: "", loadChildren: () => import("./views/pages/ingresar/ingresar.module").then(m => m.IngresarModule), canActivate: [IsLoginGuard]
      },
      {
        path: "", loadChildren: () => import("./views/pages/registrar/registrar.module").then(m => m.RegistrarModule), canActivate: [IsLoginGuard]
      },
      {
        path: "cliente", loadChildren: () => import("./views/pages/cliente/cliente.module").then(m => m.ClienteModule), canActivate: [AuthGuard]
      },
      {
        path: "admin", loadChildren: () => import("./views/pages/admin/admin.module").then(m => m.AdminModule), canActivate: [AuthGuard]
      }
    ]
  },
  { path: '**', redirectTo: 'error', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'top', useHash : true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
