import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { CatalogoService } from 'src/app/services/catalogo.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';

@Component({
  selector: 'app-carousel',
  templateUrl: './carousel.component.html',
  styleUrls: ['./carousel.component.scss']
})
export class CarouselComponent implements OnInit {

  autoPlayExampleOptions: OwlOptions = {
    items:4,
    loop:true,
    margin:10,
    autoplay:true,
    autoplayTimeout:3000,
    autoplayHoverPause:true,
    responsive:{
      0:{
          items:2
      },
      1500:{
          items:3
      },
      3000:{
          items:4
      }
    },
    autoHeight: false,
    autoWidth: false
  }

  slidesStore = [];

  private mensajes: MensajesComponent = new MensajesComponent(this.router);

  constructor(private catalogoService: CatalogoService, private router:Router) { }

  ngOnInit(): void {
    LoaderComponent.status = true;
    this.catalogoService.getNovedades().subscribe(
      response => {
        let no: number = 0;
        for(let producto of response){
          no +=
          this.slidesStore.push(
            {
              id:no,
              src:producto.imgUrl,
              alt:producto.nombre,
              title:producto.nombre
            }
          );
        }
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

}
