import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { QuejasService } from 'src/app/services/quejas.service';
import { Utilidades } from 'src/app/services/utilidades';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';
import * as StompJs from '@stomp/stompjs';
import * as SockJS from 'sockjs-client';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.scss']
})
export class MessagesComponent implements OnInit {

  private utilidades: Utilidades = new Utilidades();
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public mensajeSelect: any;
  public respuesta: FormControl;
  public respuestas: any[];
  public static mensajes: any[];
  public paginador: any;
  public paginadorRespuestas: any;
  public sizeRespuestas: number = 2;
  public static sizeMessages: number;
  public static clientStomp: StompJs.Client;

  constructor(private router: Router, private quejasService: QuejasService, private ngbModal: NgbModal) { }

  ngOnInit(): void {
    MessagesComponent.sizeMessages = 0;
    this.respuestas = [];
    MessagesComponent.clientStomp = null;
    this.respuesta = new FormControl("", [Validators.required]);
    this.mensajeSelect = null;
    if (this.utilidades.getUsuarioLogeado()) {
      this.connectSocketMessages();
      this.suscribeMessages();
      this.getMensajesNotView(1, 4);
    }
  }

  private suscribeMessages(): void {
    let _this = this;
    if (_this.utilidades.getUsuarioLogeado().roles[0].authority == "ROLE_ADMIN") {
      MessagesComponent.clientStomp.onConnect = function () {
        MessagesComponent.clientStomp.subscribe("/topic/messagesAdmin", function (message) {
         _this.pushNotification(message);
        });
      }
    } else {
      MessagesComponent.clientStomp.onConnect = function () {
        MessagesComponent.clientStomp.subscribe("/user/topic/messagesUser", function (message) {
         _this.pushNotification(message);
        });
      }
    }
  }

  private pushNotification(message: any): void {
    this.getMensajesNotView(1, 4);
    const Toast = swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', swal.stopTimer)
        toast.addEventListener('mouseleave', swal.resumeTimer)
      }
    });
    let response: any = JSON.parse(message.body);
    Toast.fire({
      icon: 'warning',
      title: response.titulo,
      text: response.descripcion.substring(0,20) + "..."
    });
  }

  private connectSocketMessages(): void {
    if (MessagesComponent.clientStomp) {
      MessagesComponent.clientStomp.deactivate();
    }
    MessagesComponent.clientStomp = null;
    MessagesComponent.clientStomp = new StompJs.Client({
      brokerURL: "ws://" + environment.socketEndPoint + "/socketConnection?token=Bearer " + this.utilidades.getToken(),
      reconnectDelay: 5000,
      onDisconnect: () => {
        this.ngOnInit();
      }
    });
    if (typeof WebSocket !== "function") {
      MessagesComponent.clientStomp.webSocketFactory = function () {
        return new SockJS("http://" + this.utilidades.endPointSock + "/socketConnection?token=Bearer " + this.utilidades.getToken());
      }
    }
    MessagesComponent.clientStomp.activate();
  }

  public paginadorInit(response: any, isRespuestas: boolean): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    if(isRespuestas) {
      this.paginadorRespuestas = {
        paginaActual: paginaActual,
        totalPaginas: totalPaginas,
        totalRegistros: response.totalElements,
        cantidadRegistros: response.size,
        paginaSiguiente: paginaSiguiente,
        paginaAnterior: paginaAnterior
      };
    } else {
      this.paginador = {
        paginaActual: paginaActual,
        totalPaginas: totalPaginas,
        totalRegistros: response.totalElements,
        cantidadRegistros: response.size,
        paginaSiguiente: paginaSiguiente,
        paginaAnterior: paginaAnterior
      };
    }
  }

  public getRespuestasByQuejaId(page: number, quejaId: number) : void {
    LoaderComponent.status = true;
    this.quejasService.getRespuestasByQuejaId(page, this.sizeRespuestas, quejaId).subscribe(
      response => {
        this.respuestas = response.content;
        this.paginadorInit(response, true);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
      }
    );
  }

  public createRespuesta() : void {
    LoaderComponent.status = true;
    if(this.respuesta.valid) {
      this.quejasService.createRespuesta(this.mensajeSelect.id, this.respuesta.value).subscribe(
        response => {
          this.respuesta.reset();
          this.getRespuestasByQuejaId(1, this.mensajeSelect.id);
          swal.fire("¡Respuesta guardada!", "", "success");
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
        }
      );
    }
  }

  public getRolUser(): string {
    return this.utilidades.getUsuarioLogeado().roles[0].authority;
  }

  public getMensajes(): any {
    return MessagesComponent.mensajes;
  }

  public openMensaje(content: any, mensaje: any): void {
    this.mensajeSelect = mensaje;
    if (this.utilidades.getUsuarioLogeado().roles[0].authority == 'ROLE_CLIENTE') {
      this.respuestasViews([mensaje.id]);
    } else if (this.utilidades.getUsuarioLogeado().roles[0].authority == 'ROLE_ADMIN') {
      this.quejasViews([mensaje.id]);
      this.getRespuestasByQuejaId(1, mensaje.id);
    }
    this.ngbModal.open(content, { size: "xl" });
  }

  public quejasViews(quejasIds: number []) : void {
    LoaderComponent.status = true;
    this.quejasService.quejasViews(quejasIds).subscribe(
      response => {
        this.getMensajesNotView(1, 4);
        if(quejasIds.length > 1) {
          swal.fire("¡Bandeja limpia!", "", "success");
        }
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
      }
    );
  }

  public respuestasViews(respuestaIds: number[]): void {
    LoaderComponent.status = true;
    this.quejasService.respuestasViews(respuestaIds).subscribe(
      response => {
        this.getMensajesNotView(1, 4);
        if(respuestaIds.length > 1) {
          swal.fire("¡Bandeja limpia!", "", "success");
        }
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public clearBandeja(): void {
    if (this.getMensajes() && this.getMensajes().length > 0) {
      if (this.utilidades.getUsuarioLogeado().roles[0].authority == 'ROLE_CLIENTE') {
        LoaderComponent.status = true;
        let respuestasIds: number[] = [];
        for (let mensaje of this.getMensajes()) {
          respuestasIds.push(mensaje.id);
        }
        this.quejasService.respuestasViews(respuestasIds).subscribe(
          response => {
            swal.fire("¡Bandeja limpia!", "", "success");
            this.getMensajesNotView(1, 4);
            LoaderComponent.status = false;
          },
          error => {
            this.mensajesComponent.mensajesError(error);
          }
        );
      } else if (this.utilidades.getUsuarioLogeado().roles[0].authority == 'ROLE_ADMIN') {
        LoaderComponent.status = true;
        let quejasIds: number[] = [];
        for (let queja of this.getMensajes()) {
          quejasIds.push(queja.id);
        }
        this.quejasViews(quejasIds);
      }
    } else {
      swal.fire("¡Tu bandeja ya esta limpia!", "", "info");
    }
  }

  public getSizeMessagesNotView(): number {
    return MessagesComponent.sizeMessages;
  }

  public getMensajesNotView(page: number, size: number): void {
    if (this.utilidades.getUsuarioLogeado().roles[0].authority == 'ROLE_CLIENTE') {
      LoaderComponent.status = true;
      this.quejasService.getRespuestas(page, size).subscribe(
        response => {
          MessagesComponent.mensajes = response.content;
          this.paginadorInit(response, false);
          MessagesComponent.sizeMessages = response.totalElements;
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
        }
      );
    } else if (this.utilidades.getUsuarioLogeado().roles[0].authority == 'ROLE_ADMIN') {
      LoaderComponent.status = true;
      this.quejasService.getAllQuejasNotView(page, size).subscribe(
        response => {
          MessagesComponent.mensajes = response.content;
          this.paginadorInit(response, false);
          MessagesComponent.sizeMessages = response.totalElements;
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
        }
      );
    }
  }

}
