import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Utilidades } from '../services/utilidades';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CatalogoService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public updateCategoria(name: string, categoriaId: number): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/categoria/update/" + name + "/" + categoriaId, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getProductsByExistenciasInZero(page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getByExistenciasInZero/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getProductsByState(state: boolean, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getByState/" + state.valueOf() + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getProductsByType(typeId: number, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getByType/" + typeId + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getStatesProducts(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getStatesProducts", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getProductNotStateById(productId): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getNotState/" + productId, {headers : this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getProductosByPedidoId(pedidoId : number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getByPedidoId/" + pedidoId, {headers : this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getByProductoId(productoId: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/getById/" + productoId, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getNovedades(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/find/novedades").pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updateExistencia(existencia: any): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/productos/existencia/update", existencia, {headers : this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public deleteExistencia(existenciaId: number): Observable<any>{
    return this.http.delete(environment.backEndPoint + "/api/productos/existencia/delete/" + existenciaId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(error => {
        return throwError(error);
      })
    );
  }

  public createExistencia(existencia: any): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/productos/existencia/create", existencia, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updateProduct(producto: any): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/productos/update", producto, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public changeStateProduct(productoId: number, state: boolean): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/productos/changeState/" + productoId + "/" + state, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findAllExistenciasTypes(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/findExistenciasTypes", {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error =>{
          return throwError(error);
        }
      )
    );
  }

  public createProduct(product: any): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/productos/create", product, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findAllProductosNotState(nombre: string, pagina: number, cantidad: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/consultarSinEstado/" + nombre + "/" + pagina + "/" + cantidad, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findForTipo(pagina: number, cantidad: number, tipo: string): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/productos/find/tipo/" + tipo + "/" + pagina + "/" + cantidad).pipe(
      catchError(error => {
        return throwError(error);
      })
    );
  }

  public findProductos(pagina: number, cantidad: number, nombre: string): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/productos/consultar/" + nombre + "/" + pagina + "/" + cantidad).pipe(
      catchError(error => {
        return throwError(error);
      })
    );
  }

  public findCategorias(): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/categoria/findAll").pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findTypesByName(name: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/categoria/findByName/" + name + "/" + page + "/" + size,{headers : this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createType(name: string): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/categoria/create/" + name, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
