import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class CotizacionService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public findByDate(dateStart: Date, dateEnd: Date, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/findByDate/" + dateStart.toISOString().substring(0, 10) + "/" + dateEnd.toISOString().substring(0, 10) + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findByUserName(userName: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/findByUserName/" + userName + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findByState(state: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/findByState/" + state + "/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public totalOrderGroupByState(): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/cotizacion/totalOrderGroupByState", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findAllHistoryStates(cotizacionId: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/findHistoryStates/" + cotizacionId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public changeState(state: String, cotizacionId: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/changeState/" + state + "/" + cotizacionId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getAllStates(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/getAllStates", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    )
  }

  public findById(cotizacionId: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/findById/" + cotizacionId, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getAll(page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/get/all/" + page + "/" + size, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public cotizacionFinishEdit(): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/cotizacion/finish/edit", null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public sendReportCotizacion(): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/cotizacion/send/reportCotizacion", null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public deleteProductoCotizacion(productoCotizacionId: number): Observable<any>{
    return this.http.delete(environment.backEndPoint + "/api/cotizacion/delete/producto/" + productoCotizacionId, {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public deleteCotizacion(): Observable<any>{
    return this.http.delete(environment.backEndPoint + "/api/cotizacion/delete/cotizacion", {headers:this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public updateCantidad(cotizacionProductoId: number, cantidad: number): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/cotizacion/update/productoCantidad/" + cotizacionProductoId + "/" + cantidad, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public addProducto(productoId: number, cantidad: number): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/cotizacion/addProducto/" + productoId + "/" + cantidad, null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getCotizacion(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/cotizacion/get", {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public createCotizacion(): Observable<any>{
    return this.http.post(environment.backEndPoint + "/api/cotizacion/create", null, {headers: this.utilidades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
