import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { AuthGuard } from './core/guard/auth.guard';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { LayoutModule } from './views/layout/layout.module';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { MensajesComponent } from './views/mensajes/mensajes.component';
import { LoaderComponent } from './views/loader/loader.component';
import { NosotrosComponent } from './views/pages/nosotros/nosotros.component';
import { ContactanosComponent } from './views/pages/contactanos/contactanos.component';
@NgModule({
  declarations: [
    AppComponent,
    MensajesComponent,
    LoaderComponent,
    NosotrosComponent,
    ContactanosComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    NgbModule,
    LayoutModule,
    HttpClientModule
  ],
  providers: [AuthGuard,
    {
      provide: LocationStrategy,
      useClass: HashLocationStrategy
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }
