import { Injectable } from '@angular/core';
import { CanActivate, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Router } from '@angular/router';
import { UsuarioService } from 'src/app/services/usuario.service';
import { Utilidades } from 'src/app/services/utilidades';
import { MessagesComponent } from 'src/app/views/layout/navbar/messages/messages.component';
import { NotificationsComponent } from 'src/app/views/layout/navbar/notifications/notifications.component';
import { SidebarComponent } from 'src/app/views/layout/sidebar/sidebar.component';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import  swal from 'sweetalert2';
import { MENU, MENU_ADMIN, MENU_CLIENT } from '../../views/layout/sidebar/menu';

@Injectable()
export class AuthGuard implements CanActivate {

  private utilidades: Utilidades = new Utilidades();
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);

  constructor(private router: Router, private usuarioService: UsuarioService) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if(this.utilidades.getUsuarioLogeado()){
      LoaderComponent.status = true;
      this.usuarioService.validarToken(this.utilidades.getUsuarioLogeado().token).subscribe(
        response => {
          LoaderComponent.status = false;
          let status: boolean = response.mensaje;
          if(status == false){
            SidebarComponent.menuItems = MENU;
            NotificationsComponent.clientStomp.deactivate();
            NotificationsComponent.clientStomp = null;
            MessagesComponent.clientStomp.deactivate();
            MessagesComponent.clientStomp = null;
            this.utilidades.deleteData();
            swal.fire("¡Inicia sesión por favor!", "", "error");
            this.router.navigate(['/']);
            return false;
          }
        },
        error => {
          SidebarComponent.menuItems = MENU;
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
          return false;
        }
      );
      if(this.utilidades.getUsuarioLogeado().roles[0].authority == "ROLE_CLIENTE"){
        SidebarComponent.menuItems = MENU_CLIENT;
      } else if (this.utilidades.getUsuarioLogeado().roles[0].authority == "ROLE_ADMIN"){
        SidebarComponent.menuItems = MENU_ADMIN;
      }
      return true;
    } else {
      if(this.utilidades.getUsuarioLogeado()){
        this.utilidades.deleteData();
      }
      SidebarComponent.menuItems = MENU;
      swal.fire("¡Inicia sesión por favor!", "", "error");
      this.router.navigate(['/']);
      return false;
    }
  }
}
