import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Utilidades } from './utilidades';

@Injectable({
  providedIn: 'root'
})
export class VentasService {

  private utiliades: Utilidades = new Utilidades();

  constructor(private http : HttpClient) { }

  public endPaymentSale(endPaymentSalePayload: any): Observable<any>{
    return this.http.put(environment.backEndPoint + "/api/venta/endPaymentSale", endPaymentSalePayload, {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public findAllStatesVenta(): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/venta/get/statesVenta", {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public geVentaByVentaId(ventaId : number) : Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/ByVentaId/" + ventaId, {headers : this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getVentasBig(estado: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/ventasBig/" + estado + "/" + page + "/" + size, {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getVentasByFechaInicioAndFechaFin(estado: string, fechaInicio: Date, fechaFin: Date, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/byFechaInicioAndFechaFin/" + estado + "/" + page + "/" + size + "?fechaInicio=" + fechaInicio.toISOString().substring(0, 10) + "&fechaFin=" + fechaFin.toISOString().substring(0, 10), {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getVentasByClienteApodo(apodo: string, page: number, size: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/byClienteApodo/" + page + "/" + size + "?apodo=" + apodo, {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public changeEstadoByVenta(ventaId: number, estado: string, fechaEstimada: Date): Observable<any>{
    let fechaEstimadaParam: string = fechaEstimada != null ? "?fechaEstimada=" + fechaEstimada.toISOString() : "";
    return this.http.put(environment.backEndPoint + "/api/venta/change/estado/" + ventaId + "/" + estado + fechaEstimadaParam, null, {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getVentasAmountByEstados(): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/amount/estado", {headers : this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

  public getVentasByEstado(estado: string, pagina: number, cantidad: number): Observable<any>{
    return this.http.get(environment.backEndPoint + "/api/venta/get/byEstado/" + estado + "/" + pagina + "/" + cantidad, {headers: this.utiliades.getHeaders()}).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
