import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Utilidades } from 'src/app/services/utilidades';
import swal from 'sweetalert2';
import { LoaderComponent } from '../loader/loader.component';

@Component({
  selector: 'app-mensajes',
  templateUrl: './mensajes.component.html',
  styleUrls: ['./mensajes.component.scss']
})
export class MensajesComponent implements OnInit {

  private utilidades: Utilidades = new Utilidades();

  constructor(private router: Router) { }

  ngOnInit(): void {
  }

  public mensajesError(error: any): void {
    if (error.status == 500) {
      swal.fire("¡Error!", error.error.error, "error");
    } else if (error.status == 400) {
      if(Array.isArray(error.error)){
        swal.fire("¡Error!", error.error[0], "error");
      } else{
        if (Array.isArray(error.error.error)) {
          let errores: string = "<ul class='list-group'>";
          for (let index = 0; index < error.error.error.length; index++) {
            errores = errores + "<li class='list-group-item'>" + error.error.error[index] + "</li>";
          }
          errores = errores + "</ul>";
          swal.fire("¡Error!", errores, "error");
        } else {
          swal.fire("¡Error!", error.error.error, "error");
        }
      }
    } else if (error.status == 404) {
      swal.fire("¡Error!", error.error.error, "error");
    } else if (error.status == 403) {
      this.utilidades.deleteData();
      this.router.navigate(["/"]);
      swal.fire("¡Error!", "¡Por favor inicia sesión!", "error");
    } else {
      swal.fire("¡Error!", "Upps ha ocurrido un error, intentalo de nuevo.", "error");
    }
    console.log(error);
    LoaderComponent.status = false;
  }

}
